<template>
  <div v-show="detailShow" class="detail" transition="fade">
    <div class="tab">
      <div class="tab-item icon">
        <i @click="hideDetail">×</i>
      </div>
      <div class="tab-item tab-expense">
        <router-link to="/weibo/expense">支出</router-link>
      </div>
      <div class="tab-item tab-earning">
        <router-link to="/weibo/earning">收入</router-link>
      </div>
      <div class="tab-item icon">
        <i @click="addMoney">+</i>
      </div>
    </div>
    <div class="content">
      <router-view></router-view>
    </div>
    <div class="detail-input">
      <div class="top-line">
        <button class="calendarBtn" @click.stop="open($event)">{{calendar.items.value}}</button>
        <button class="newMoney">¥&nbsp;&nbsp;{{newMoney}}</button>
      </div>
      <div class="detail-line">
        <button @click="keyBroad(1,1)">1</button>
        <button @click="keyBroad(1,2)">2</button>
        <button @click="keyBroad(1,3)">3</button>
      </div>
      <div class="detail-line">
        <button @click="keyBroad(1,4)">4</button>
        <button @click="keyBroad(1,5)">5</button>
        <button @click="keyBroad(1,6)">6</button>
      </div>
      <div class="detail-line">
        <button @click="keyBroad(1,7)">7</button>
        <button @click="keyBroad(1,8)">8</button>
        <button @click="keyBroad(1,9)">9</button>
      </div>
      <div class="detail-line">
        <button @click="keyBroad(2,null)" style="font-size: 20px;">清零</button>
        <button @click="keyBroad(1,0)">0</button>
        <button @click="keyBroad(3,null)">.</button>
      </div>
    </div>
    <Calendar :show="calendar.show" :type="calendar.items.type" :value="calendar.items.value" :x="calendar.x" :y="calendar.y"
          :begin="calendar.items.begin" @cancel="dateCancel" @ok="dateOk" :end="calendar.items.end" :sep="calendar.items.sep"
          :single="calendar.items.single" :rangeValue='calendar.items.rangeValue' :autoclose='calendar.items.autoclose'>
    </Calendar>
  </div>
</template>

<script>
import Calendar from './calendar'
var thisMonth = new Date().getMonth() >= 9 ? (new Date().getMonth() + 1).toString() : '0' + (new Date().getMonth() + 1)
var thisDay = new Date().getDate() >= 9 ? (new Date().getDate() + 1).toString() : '0' + (new Date().getDate() + 1)
export default {
  components: {
    Calendar
  },
  data () {
    return {
      totalEraning: 14,
      totalExpense: 20,
      msg: 'fit',
      chart: null,
      detailShow: true,
      newMoney: '',
      inputValue: 0,
      calendar: {
        show: false,
        x: 0,
        y: 0,
        items: {
          type: 'date',
          begin: '2015/08/20',
          end: [new Date().getFullYear(), thisMonth, thisDay].join('/'),
          value: [new Date().getFullYear(), thisMonth, thisDay].join('/'),
          text: [new Date().getFullYear(), thisMonth, thisDay].join('/'),
          sep: '/',
          single: true,
          autoclose: true,
          weeks: ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'],
          months: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec']
        }
      }
    }
  },
  methods: {
    showDetail () {
      this.detailShow = true
    },
    hideDetail () {
      this.detailShow = false
      this.newMoney = ''
    },
    addWeight () {
      this.inputValue = parseFloat(this.newMoney)
      console.log(typeof this.calendar.items.value, this.calendar.items.value)
      if (isNaN(this.inputValue)) {
        this.detailShow = false
        this.newMoney = ''
      } else {
        this.weight.push([this.calendar.items.value, this.inputValue])
        this.detailShow = false
        this.newMoney = ''
      }
    },
    keyBroad (tip, value) {
      if (tip === 1) {
        if ((value !== 0 || this.newMoney.length !== 0) && (this.newMoney.indexOf('.') !== this.newMoney.length - 3 || this.newMoney.indexOf('.') === -1) && this.newMoney.length < 7) {
          this.newMoney = this.newMoney + value.toString()
        }
      } else if (tip === 2) {
        this.newMoney = ''
      } else if (tip === 3) {
        if (this.newMoney.indexOf('.') === -1) {
          this.newMoney = this.newMoney + '.'
        }
      }
    },
    open (e, type) {
      // 设置类型
      this.calendar.show = true
      this.calendar.x = e.target.offsetLeft
      this.calendar.y = e.target.offsetTop + e.target.offsetHeight + 8
    },
    dateCancel (obj) {
      this.calendar.show = obj.show
    },
    dateOk (obj) {
      this.calendar.items.text = obj.values[0]
      this.calendar.items.value = obj.values[0]
    }
  }
}
</script>
<style scoped>
.detail {
  position: relative;
  background: #fff;
  width: 100%;
  overflow-x: hidden;
  min-height: 100%;
}
.detail-main {
  height: 100px;
  line-height: 100px;
  width: 100%;
}
.tab {
  position: relative;
  display: flex;
  width: 100%;
  height: 40px;
  line-height: 40px;
}
.icon {
  margin: 0 10px;
  flex: 10%;
  color: rgba(69, 89, 99, 0.43);
  text-align: left;
  font-family: 'Times New Roman';
  font-size: 43px;
  font-weight: lighter;
}
.tab-expense {
  flex: 40%;
  text-align: right;
  padding-right: 23px;
}
.tab-earning {
  flex: 40%;
  text-align: left;
  padding-left: 23px;
}
.tab-item > a {
  font-size: 1em;
  line-height: 10px;
  color: #455963;
  font-weight: normal;
}
.tab-item > a.router-link-active {
  color: rgb(245, 180, 83);
  font-weight: normal;
}
.weightShow {
  position: absolute;
  top: 25px;
  left: 35%;
  width: 100px;
  height: 40px;
  font-size: 1.8em;
  text-align: center;
  color: #0276b1;
  padding-bottom: 28px;
  border-bottom: thick dotted #5e7a88;
}
.detail-add {
  position: absolute;
  top: 20px;
  right: 20px;
  font-style: normal; 
  width: 40px;
  height: 40px;
  color: rgba(49, 78, 95, .95);
  background: rgba(255,255,255, 0.8);
  font-family: 'Times New Roman';
  line-height: 40px; 
  font-size: 3em;
  font-weight: bolder;
  border-radius: 20px 20px;
}
.detail-input {
  width: 100%;
  position: absolute;
  bottom: -0.1px;
  left: 0;
  right: 0;
}
.detail-line{
  width: 100%;
  display: flex;
}
.top-line {
  width: 100%;
  display: flex;
  justify-content: center;
  background-color: rgba(242, 242, 242, 0.48);
}
.top-line button {
  display: block;
  flex: 33.33%;
  height: 33px;
  padding: 5px;
  color: #455963;
  background-color: rgba(235, 235, 235, 0.48);
  font-weight: lighter;
  outline:none;
  border-style: none;
}
.newMoney {
  font-size: 20px;
  text-align: right;
  font-weight: normal;  
}
.detail-line button {
  display: block;
  flex: 33.33%;
  font-size: 26px;
  padding: 5px;
  color: #455963;
  background-color: rgba(235, 235, 235, 0.48);
  font-weight: lighter;
  outline:none;
  border:0.1px solid #fff;
}
.detail-line button:hover {
  background-color: rgba(235, 235, 235, 1);
}
.calendarBtn{
  font-size: 1em;
  background-image:url(data:image/svg+xml;base64,);
  background-repeat: no-repeat;
  background-position: 18px center;
}
</style>

